	module.exports = {"data":{"wordpressPage":{"title":"Pain management for medical abortion before 13 weeks gestation","content":"<h2>Recommendation:</h2>\n<ul>\n<li>Always offer pain medication to women undergoing medical abortion.</li>\n<li>Non-steroidal anti-inflammatory drugs (NSAIDs) such as ibuprofen are recommended and provide similar relief whether taken prophylactically or at the time cramping begins.</li>\n<li>Narcotic analgesics and non-pharmacologic pain management measures may be helpful.</li>\n<li>Paracetamol should not be used unless an allergy or contraindication to NSAIDs exists.</li>\n</ul>\n<h3>Strength of recommendation: Strong</h3>\n<h3></h3>\n<h3>Quality of evidence: Low</h3>\n<p>Pain during medical abortion before 13 weeks gestation</p>\n<p>Pain is the most commonly reported side effect of medical abortion (Fiala et al., 2014). In one study of 6,755 women using medical abortion up to 63 days gestation, 78.4% reported moderate or severe pain and cramping (Goldstone, Michelson, &amp; Williamson, 2012). Similarly, a 2006 systematic review of five large British and American case series of analgesia use during medical abortion concluded that 75% of women experience pain requiring narcotic analgesia (Penney, 2006).  Patient characteristics associated with more pain include increasing gestational age, younger patient age, nulliparity, no previous vaginal deliveries, and history of dysmenorrhea (Suhonen, Tikka, Kivinen, &amp; Kauppila, 2011; Teal, Dempsey-Fanning, &amp; Westhoff, 2007; Westhoff, Dasmahapatra, Winikoff, &amp; Clarke, 2000).</p>\n<p>There are few trials assessing effectiveness of pain management strategies during medical abortion before 13 weeks gestation. Neither pain nor its treatment are systematically reported in clinical trials of medical abortion; where these data are reported, multiple regimens and treatment protocols have been used, rendering them difficult to compare (Fiala et al., 2014; Jackson &amp; Kapp, 2011).</p>\n<p>Medications for pain management</p>\n<p>Two small randomized controlled trials indicate that ibuprofen is more effective than placebo (Avraham, Gat, Duvdevani, Haas, &amp; Frenkel, 2012) or acetaminophen (Livshits et al., 2009) in relieving medical abortion pain in women with pregnancies of less than seven weeks gestation. Pre-treatment with ibuprofen is no better for pain management than treatment once cramping starts (Raymond et al., 2013). Narcotic analgesics are another option for pain control, although their effectiveness and the optimal drug, dose and timing is not known. One potential strategy is to provide women with NSAIDs and narcotic analgesics and advise them to take NSAIDs once cramping starts, continue to use NSAIDs as needed, and utilize narcotic analgesics as an adjunctive treatment.</p>\n<p>Non-pharmacologic pain management</p>\n<p>There are no comparative trials evaluating the benefit of non-pharmacologic pain management strategies for medical abortion before 13 weeks gestation. However, experts recommend adjunctive non-pharmacologic measures to improve women’s comfort during a medical abortion, including thorough education about expected pain and bleeding (Teal, Dempsey-Fanning, &amp; Westhoff, 2007), a supportive environment and application of  a heating pad or hot water bottle to the lower abdomen (Akin, et al., 2001). These modalities are to be employed in addition to—not as substitutes for—pain medications.</p>\n<h3>References</h3>\n<p>Akin, M. D., Weingard, K. W., Hengehold, D. A., Goodale, M. B., Hinkle, R. T., &amp; Smith, R. P. (2001). Continuous low-level topical heat in the treatment of dysmenorrhea. <em>Obstetrics &amp; Gynecology, 97, </em>343-349.</p>\n<p>Avraham, S., Gat, I., Duvdevani, N., Haas, J., &amp; Frenkel, Y. (2012). Pre-emptive effect of ibuprofen versus placebo on pain relief and success rates of medical abortion: A double-blind, randomized, controlled study. <em>Fertility and Sterility, 97</em>, 612-615.</p>\n<p>Fiala, C., Cameron, S., Bombas, T., Parachini, M., Saya, L., &amp; Gemzell-Danielsson, K. (2014). Pain during medical abortion, the impact of the regimen: A neglected issue? A review. <em>European Journal of Contraception and Reproductive Health Care, 19</em>(6), 404-419.</p>\n<p><a href=\"https://venus.ipas.org/library/fulltext/GoldstoneMJA2012.pdf\">Goldstone, P., Michelson, J., &amp; Williamson, E. (2012). Early medical abortion using low-dose mifepristone followed by buccal misoprostol: A large Australian observational study. <em>Medical Journal of Australia, 197</em>(5), 282-286. </a></p>\n<p>&nbsp;</p>\n<p>Jackson, E., &amp; Kapp, N. (2011). Pain control in first-trimester and second-trimester medical termination of pregnancy: A systematic review. <em>Contraception</em>, <em>83</em>(2), 116-126.</p>\n<p>&nbsp;</p>\n<p>Livshits, A., Machtinger, R., David, L. B., Spira, M., Moshe-Zahav, A., &amp; Seidman, D. S. (2009). Ibuprofen and paracetamol for pain relief during medical abortion: A double-blind randomized controlled study. <em>Fertility and Sterility</em>, <em>91</em>(5), 1877-1880.</p>\n<p>&nbsp;</p>\n<p>Penney, G. (2006). Treatment of pain during medical abortion. <em>Contraception, 74, </em>45-47.</p>\n<p>&nbsp;</p>\n<p>Raymond, E.G., Weaver, M.A., Louie, K.S., Dean, G., Porsch, L., Lichtenberg, E.S., &#8230;Arnesen, M. (2013). Prophylactic compared with therapeutic ibuprofen analgesia in first-trimester medical abortion. <em>Obstetrics &amp; Gynecology</em>, <em>122</em>(3), 558-564.</p>\n<p>&nbsp;</p>\n<p>Suhonen, S., Tikka, M., Kivinen, S., &amp; Kauppila, T. (2011). Pain during medical abortion: Predicting factors from gynecologic history and medical staff evaluation of severity. <em>Contraception, 83</em>, 357-361.</p>\n<p>&nbsp;</p>\n<p>Teal, S. B., Dempsey-Fanning, A., &amp; Westhoff, C. (2007). Predictors of acceptability of medication abortion. <em>Contraception, 75</em>, 224-229.</p>\n<p>&nbsp;</p>\n<p>Westhoff, C., Dasmahapatra, R., Winikoff, B., &amp; Clarke, S. (2000). Predictors of analgesia use during supervised medical abortion. The Mifepristone Clinical Trials Group. <em>Contraception</em>, <em>61</em>(3), 225-229.</p>\n<p>&nbsp;</p>\n<p>Westhoff, C., Dasmahapatra, R., &amp; Schaff, E. (2000). Analgesia during at-home use of misoprostol as part of a medical abortion regimen. <em>Contraception,</em> <em>62</em>(6), 311-314.</p>\n<p>&nbsp;</p>\n<p>World Health Organization. (2014). <em>Clinical practice handbook for safe abortion</em>. Geneva: World Health Organization Press.</p>\n<p>&nbsp;</p>\n","fields":{"path":"pain-management/ma-before-13-weeks"},"acf":{"last_reviewed":"20171025","landing_page":null}},"parent":{"title":"Pain management","fields":{"path":"pain-management"},"wordpress_parent":159},"grandparent":null,"site":{"id":"Site","siteMetadata":{"title":"Ipas Clinical Updates"}}},"pathContext":{"id":"175fb61c-357c-5b83-a623-b85d7d3d8516","title":"Pain management for medical abortion before 13 weeks gestation","content":"<h2>Recommendation:</h2>\n<ul>\n<li>Always offer pain medication to women undergoing medical abortion.</li>\n<li>Non-steroidal anti-inflammatory drugs (NSAIDs) such as ibuprofen are recommended and provide similar relief whether taken prophylactically or at the time cramping begins.</li>\n<li>Narcotic analgesics and non-pharmacologic pain management measures may be helpful.</li>\n<li>Paracetamol should not be used unless an allergy or contraindication to NSAIDs exists.</li>\n</ul>\n<h3>Strength of recommendation: Strong</h3>\n<h3></h3>\n<h3>Quality of evidence: Low</h3>\n<p>Pain during medical abortion before 13 weeks gestation</p>\n<p>Pain is the most commonly reported side effect of medical abortion (Fiala et al., 2014). In one study of 6,755 women using medical abortion up to 63 days gestation, 78.4% reported moderate or severe pain and cramping (Goldstone, Michelson, &amp; Williamson, 2012). Similarly, a 2006 systematic review of five large British and American case series of analgesia use during medical abortion concluded that 75% of women experience pain requiring narcotic analgesia (Penney, 2006).  Patient characteristics associated with more pain include increasing gestational age, younger patient age, nulliparity, no previous vaginal deliveries, and history of dysmenorrhea (Suhonen, Tikka, Kivinen, &amp; Kauppila, 2011; Teal, Dempsey-Fanning, &amp; Westhoff, 2007; Westhoff, Dasmahapatra, Winikoff, &amp; Clarke, 2000).</p>\n<p>There are few trials assessing effectiveness of pain management strategies during medical abortion before 13 weeks gestation. Neither pain nor its treatment are systematically reported in clinical trials of medical abortion; where these data are reported, multiple regimens and treatment protocols have been used, rendering them difficult to compare (Fiala et al., 2014; Jackson &amp; Kapp, 2011).</p>\n<p>Medications for pain management</p>\n<p>Two small randomized controlled trials indicate that ibuprofen is more effective than placebo (Avraham, Gat, Duvdevani, Haas, &amp; Frenkel, 2012) or acetaminophen (Livshits et al., 2009) in relieving medical abortion pain in women with pregnancies of less than seven weeks gestation. Pre-treatment with ibuprofen is no better for pain management than treatment once cramping starts (Raymond et al., 2013). Narcotic analgesics are another option for pain control, although their effectiveness and the optimal drug, dose and timing is not known. One potential strategy is to provide women with NSAIDs and narcotic analgesics and advise them to take NSAIDs once cramping starts, continue to use NSAIDs as needed, and utilize narcotic analgesics as an adjunctive treatment.</p>\n<p>Non-pharmacologic pain management</p>\n<p>There are no comparative trials evaluating the benefit of non-pharmacologic pain management strategies for medical abortion before 13 weeks gestation. However, experts recommend adjunctive non-pharmacologic measures to improve women’s comfort during a medical abortion, including thorough education about expected pain and bleeding (Teal, Dempsey-Fanning, &amp; Westhoff, 2007), a supportive environment and application of  a heating pad or hot water bottle to the lower abdomen (Akin, et al., 2001). These modalities are to be employed in addition to—not as substitutes for—pain medications.</p>\n<h3>References</h3>\n<p>Akin, M. D., Weingard, K. W., Hengehold, D. A., Goodale, M. B., Hinkle, R. T., &amp; Smith, R. P. (2001). Continuous low-level topical heat in the treatment of dysmenorrhea. <em>Obstetrics &amp; Gynecology, 97, </em>343-349.</p>\n<p>Avraham, S., Gat, I., Duvdevani, N., Haas, J., &amp; Frenkel, Y. (2012). Pre-emptive effect of ibuprofen versus placebo on pain relief and success rates of medical abortion: A double-blind, randomized, controlled study. <em>Fertility and Sterility, 97</em>, 612-615.</p>\n<p>Fiala, C., Cameron, S., Bombas, T., Parachini, M., Saya, L., &amp; Gemzell-Danielsson, K. (2014). Pain during medical abortion, the impact of the regimen: A neglected issue? A review. <em>European Journal of Contraception and Reproductive Health Care, 19</em>(6), 404-419.</p>\n<p><a href=\"https://venus.ipas.org/library/fulltext/GoldstoneMJA2012.pdf\">Goldstone, P., Michelson, J., &amp; Williamson, E. (2012). Early medical abortion using low-dose mifepristone followed by buccal misoprostol: A large Australian observational study. <em>Medical Journal of Australia, 197</em>(5), 282-286. </a></p>\n<p>&nbsp;</p>\n<p>Jackson, E., &amp; Kapp, N. (2011). Pain control in first-trimester and second-trimester medical termination of pregnancy: A systematic review. <em>Contraception</em>, <em>83</em>(2), 116-126.</p>\n<p>&nbsp;</p>\n<p>Livshits, A., Machtinger, R., David, L. B., Spira, M., Moshe-Zahav, A., &amp; Seidman, D. S. (2009). Ibuprofen and paracetamol for pain relief during medical abortion: A double-blind randomized controlled study. <em>Fertility and Sterility</em>, <em>91</em>(5), 1877-1880.</p>\n<p>&nbsp;</p>\n<p>Penney, G. (2006). Treatment of pain during medical abortion. <em>Contraception, 74, </em>45-47.</p>\n<p>&nbsp;</p>\n<p>Raymond, E.G., Weaver, M.A., Louie, K.S., Dean, G., Porsch, L., Lichtenberg, E.S., &#8230;Arnesen, M. (2013). Prophylactic compared with therapeutic ibuprofen analgesia in first-trimester medical abortion. <em>Obstetrics &amp; Gynecology</em>, <em>122</em>(3), 558-564.</p>\n<p>&nbsp;</p>\n<p>Suhonen, S., Tikka, M., Kivinen, S., &amp; Kauppila, T. (2011). Pain during medical abortion: Predicting factors from gynecologic history and medical staff evaluation of severity. <em>Contraception, 83</em>, 357-361.</p>\n<p>&nbsp;</p>\n<p>Teal, S. B., Dempsey-Fanning, A., &amp; Westhoff, C. (2007). Predictors of acceptability of medication abortion. <em>Contraception, 75</em>, 224-229.</p>\n<p>&nbsp;</p>\n<p>Westhoff, C., Dasmahapatra, R., Winikoff, B., &amp; Clarke, S. (2000). Predictors of analgesia use during supervised medical abortion. The Mifepristone Clinical Trials Group. <em>Contraception</em>, <em>61</em>(3), 225-229.</p>\n<p>&nbsp;</p>\n<p>Westhoff, C., Dasmahapatra, R., &amp; Schaff, E. (2000). Analgesia during at-home use of misoprostol as part of a medical abortion regimen. <em>Contraception,</em> <em>62</em>(6), 311-314.</p>\n<p>&nbsp;</p>\n<p>World Health Organization. (2014). <em>Clinical practice handbook for safe abortion</em>. Geneva: World Health Organization Press.</p>\n<p>&nbsp;</p>\n","last_reviewed":"20171025","parent":165,"grandparent":null}}

